import type { PWAInstallElement } from "@khmyznikov/pwa-install"
import config from "../../../config"
import "./InstallButton.css"
import { Component, createMemo, createSignal, Show } from "solid-js"
// import { Portal } from "solid-js/web"
import { delay } from "../../../utils/delay"

const InstallButton: Component = () => {
  const [loading, setLoading] = createSignal(false)
  const isInstalled = createMemo(() => {
    return (
      window.matchMedia("(display-mode: standalone)").matches ||
      ("standalone" in navigator && (navigator as any).standalone === true)
    )
  })

  return (
    <Show when={!isInstalled() && config.IS_MOBILE}>
      <button
        classList={{ download: true, pulse: !loading() }}
        onClick={async () => {
          setLoading(true)
          await import("@khmyznikov/pwa-install")

          await delay(500)

          const $pwaInstall = document.querySelector(
            "pwa-install",
          ) as unknown as PWAInstallElement

          $pwaInstall.showDialog()
          setLoading(false)
        }}
      >
        <i class={loading() ? "icon-loading" : "icon-download"}></i>
      </button>
      {/* <Portal> */}
      {/* @ts-ignore */}
      {/*   <pwa-install */}
      {/*     name="Entertrained" */}
      {/*     description="Practice touch typing by retyping famous novels" */}
      {/*     manual-apple="true" */}
      {/*     manual-chrome="true" */}
      {/*     icon="/images/icon-pad.svg" */}
      {/*     install-description="This site has app functionality. Add it to your Home Screen for fullscreen view, easy access, and offline capabilities." */}
      {/*     manifest-url="/manifest.webmanifest" */}
      {/*   /> */}
      {/* </Portal> */}
    </Show>
  )
}

export default InstallButton
